.container-main{
    width: 100%;
    height: 100%;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    flex-direction: row;
    align-items: center;
    flex-flow: column;
}

.title{
    text-align: center;
    text-transform: uppercase;
    font-size: 30px; 
    color: black;
}

.order-main{
    border: 2px solid black;
    background-color: cyan;
    border-radius: 5px;
    padding: 10px;
    margin: 20px;
}

.order-id{
    text-align: center;
    text-transform: uppercase;
    font-size: 20px; 
    color: black;
}