.container{
    width: 100%;
    height: 100%;
    background-color: #f5f5f5;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    flex-direction: row;
    align-items: center;
    flex-flow: column;
}

.order{
    border: 2px solid #333;
    border-radius: 10px;
    padding: 10px;
}

.inner{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    flex-direction: row;
    align-items: center;
    flex-flow: row;
}

.item{
    background-color: black;
    color: cyan;
}

.item > h3{
    text-align: center;
    text-transform: uppercase;
    font-size: 1.5em; 
    color: yellow;
}

.item > h2{
    text-align: center;
    text-transform: uppercase;
}

.btn{
    padding: 10px 20px;
    background-color: blue;
    color: white;
    border: none;
    border-radius: 5px;
    font-size: 16px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
}

.btn:before{
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 2;
    opacity: 0.5;
    transition: opacity 0.3s ease-in-out;
}

.border-bottom{
    height: 10px;
    width: 100%;
    border-bottom: 1px solid #333;
    margin: 40px 0; 
}

.btn:after{
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    transform: translate(-50%, -50%);
    height: 0;
    width: 0;
    border-radius: 50%;
    background-color: rgba(255,0,0,0.5);
    z-index: 1;
    transition: height 0.3s ease-in-out, width 0.3s ease-in-out;
}

.btn:hover{
    opacity: 0.5;
}